import Vue from 'vue';
import Router from 'vue-router';
import store from '../store/index.js';

// import index from 'components/index/index';
// import tabList from 'components/index/tab-list';

// import issue from 'components/issue/issue';
// import message from 'components/message/message';
// import home from 'components/home/home';

// import topic from 'components/topic/topic';
// import about from 'components/about';
// import login from 'components/login';

import { Toast } from 'mint-ui';

Vue.use(Router);

if (window.localStorage.getItem('_token')) {
  let token = window.localStorage.getItem('_token');
  let user = JSON.parse(window.localStorage.getItem('_user'));
  store.commit('login', token);
  store.commit('addUser', user);
};

let routes = [
  {
    path: '/',
    redirect: '/index/all'
  },
  {
    path: '/index',
    component: resolve => {
      require(['components/index/index.vue'], resolve);
    },
    children: [
      {
        path: '',
        redirect: 'all'
      },
      {
        path: ':tab',
        component: resolve => {
          require(['components/index/tab-list.vue'], resolve);
        }
      }
    ]
  },
  {
    path: '/issue',
    component: resolve => {
      require(['components/issue/issue'], resolve);
    },
    meta: {
      requireAuth: true
    }
  },
  {
    path: '/message',
    component: resolve => {
      require(['components/message/message'], resolve);
    },
    meta: {
      requireAuth: true
    }
  },
  {
    path: '/home',
    component: resolve => {
      require(['components/home/home'], resolve);
    }
  },
  {
    path: '/topic/:id',
    component: resolve => {
      require(['components/topic/topic'], resolve);
    }
  },
  {
    path: '/about',
    component: resolve => {
      require(['components/about'], resolve);
    }
  },
  {
    path: '/login',
    component: resolve => {
      require(['components/login'], resolve);
    }
  }
];

let router = new Router({
  routes
});

router.beforeEach((to, from, next) => {
  if (to.meta.requireAuth) {
    if (store.state.token) {
      next();
    } else {
      Toast({
        message: '请先登录',
        duration: 2000
      });
      next({
        path: '/login',
        query: {
          redirect: to.fullPath
        }
      });
    }
  }
  next();
});

export default router;



